.OverviewCard {
  width: inherit;
  height: 400px;

  background-image: url('../../../../assets/Content/FirstCard/FirstCard.jpeg');
  background-size: 100%;
  background-position-y: -250px;
  background-attachment: scroll;

  padding: 120px;

  color: rgb(255, 255, 255);

  font-size: 70px;
}

.OverviewCard p {
  line-height: 50%;
}

.DefaultCard {
  width: inherit;
  height: 650px;
  background-color: #f5f5f5;
  padding: 20px;
  color: black;
}

.OddCard {
  width: inherit;
  height: 650px;
  background-color: #1f1f1f;
  padding: 20px 0px 20px 0px;
  color: white;
}

.SkillsSet {
  display: inline-block;
  vertical-align: top;
  margin: 40px;
  margin-top: 20px;
}



